import {
  Button,
  Checkbox,
  Form,
  Input,
  InputNumber,
  message,
  Switch,
} from 'antd';
import React, { useEffect } from 'react';
import { IMovie } from '../services/MoviceService';
import { useNavigate } from 'react-router-dom';
import ImgUploader from './ImgUploader';
import { useForm } from 'antd/lib/form/Form';

interface IFormProps {
  movie?: IMovie;
  onSubmit: (movie: IMovie) => Promise<string>;
}

const formItemLayout = {
  label: {
    span: 4,
  },
  wrapperCol: {
    span: 19,
    offset: 1,
  },
};

const allAreas: { label: string; value: string }[] = [
  { label: '', value: '' },
  { label: '', value: '' },
  { label: '', value: '' },
  { label: '', value: '' },
];

const AreaGroups = Checkbox.Group;

const AllTypes: { label: string; value: string }[] = [
  { label: '', value: '' },
  { label: '', value: '' },
  { label: '', value: '' },
  { label: '', value: '' },
];
const TypeGroups = Checkbox.Group;

const MovieForm: React.FC<IFormProps> = (props) => {
  const [form] = useForm();
  const { movie } = props;
  const navigate = useNavigate();
  async function handleSubmit() {
    try {
      await form.validateFields();
      const formData = form.getFieldsValue();
      const result = await props.onSubmit(formData as any);
      if (result) {
        message.error(result);
      } else {
        message.success('', 1, () => {
          navigate('/movie/list');
        });
      }
    } catch (error) {
      console.log(error);
    }
  }
  useEffect(() => {
    form.setFieldsValue(movie);
  }, [movie, form]);
  return (
    <div>
      <Form
        onFinish={handleSubmit}
        {...formItemLayout}
        style={{ width: '100%' }}
        form={form}
      >
        <Form.Item
          label=""
          name="name"
          rules={[{ required: true, message: '' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item label="" name="poster">
          <ImgUploader />
        </Form.Item>
        <Form.Item
          label=""
          name="areas"
          rules={[{ required: true, message: '' }]}
        >
          <AreaGroups options={allAreas} />
        </Form.Item>
        <Form.Item
          label=""
          name="types"
          rules={[{ required: true, message: '' }]}
        >
          <TypeGroups options={AllTypes} />
        </Form.Item>
        <Form.Item
          label="()"
          name="timeLong"
          rules={[{ required: true, message: '' }]}
        >
          <InputNumber min={1} max={9999} step={10} />
        </Form.Item>

        <Form.Item label="" name="isHot" valuePropName="checked">
          <Switch />
        </Form.Item>
        <Form.Item label="" name="isComing" valuePropName="checked">
          <Switch />
        </Form.Item>
        <Form.Item label="" name="isClassic" valuePropName="checked">
          <Switch />
        </Form.Item>
        <Form.Item label="" name="description">
          <Input.TextArea />
        </Form.Item>
        <Form.Item labelCol={{ span: 0 }} wrapperCol={{ span: 19, offset: 5 }}>
          <Button type="primary" htmlType="submit">
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
};

export default MovieForm;
